import { catchError, map } from 'rxjs/operators';
import { of } from 'rxjs';

export function removeProp(branch, ...keys) {
  const next = { ...branch };
  keys.forEach((key) => {
    delete next[key];
  });
  return next;
}

export const errorOp = catchError((response) => of(response));

export const mapOp = (success, fail) => map(
  ({ response, name }) =>
    (name !== 'AjaxError')
      ? success(response)
      : fail(response)
);

export function getRootType(type) {
  return type.replace(/_FAILED|_COMPLETE/g, '');
}

export function byIdLoadingKey(root, id) {
  return `${root}[${id}]`;
}
