import React from "react";
import { Link } from "react-router-dom";

export default function Anchor(props) {

  const { href, autoTarget } = props;

  // eslint-disable-next-line
  const isExternal = !!['http:', 'https:', 'javascript:'].find((protocol) => href.includes(protocol));
  const Component = isExternal ? 'a' : Link;

  const resultProps = {
    ...(
      isExternal
        ? { href: href }
        : { to: href }
    ),
    ...(
      autoTarget && isExternal
        ? { target: "_blank" }
        : {}
    ),
    ...props
  };

  !isExternal && delete resultProps.href;
  delete resultProps.autoTarget;

  return (
    <Component {...resultProps}>
      {props.children}
    </Component>
  );

}

Anchor.defaultProps = {
  // eslint-disable-next-line
  href: 'javascript:',
  autoTarget: true
};
